.firstContainer {
	width: auto;
	height: 200px;
	display: flex;
	align-items: center;
	margin-top: 5%;
}

.firstContainer div {
	text-align: center;
	height: 100%;
	width: auto;
	flex: 1;
	padding-left: 10%;
	padding-right: 10%;
}

.firstContainer div img {
	width:20%;
	height:auto;
}

.border-right {
	border-right: 1px solid rgba(0, 0, 0, 0.3);
}

.secondContainer {
	width: 100%;
	height: 150px;
	background-color: rgba(0, 0, 0, 0.1);
	margin-top: 4%;
}

.flexDiv {
	display: flex;
	justify-content: center;
	align-items:center;
	height: 100%;
	width: 100%;
}

.flexDiv div {
	display:flex;
	justify-content: center;
	align-items: center;
	height:auto;
	width:5%;
	margin-left:2%;
	margin-right:2%;
}

.flexDiv img {
	height: 100%;
	width: 100%;
	cursor: pointer;
}

@media screen and (max-width: 1000px) {
	.firstContainer {
		flex-direction: column;
		width: 100%;
		height: auto;
	}

	.firstContainer div {
		padding: 20%;
	}

	.flexDiv div {
		width:10%;
		height: auto;
	}
}